*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
 
}

body{
  font-family: 'Montserrat',sans-serif;
  overflow-x: hidden;
}


/* ----------------- Header section----------------------- */
.header{
    background-image: url('img/temp.jpg');
    background-size:cover;
    width: auto;
    max-height:130rem ;
    
}

.navbar{
  display: flex;
  align-items: center;
  padding: 20px;
}

.logo img{
  width: 150px;
}

nav{
  flex:1;
  text-align: right;
}


nav ul{
  display: inline-block;
  list-style-type: none;
}

nav ul li{
  display: inline-block;
  margin: 20px;
 
}

nav ul li a{
  color: #fff;
}

#MenuItems a:hover{
  color:cornsilk;
}

 .container{
  max-width: 1300px;
  margin: auto;
  padding:0 25px;
}

 .row{
  display: flex;
}

div.colum-4{
  flex-basis: 30rem;
  justify-content: space-around;
  align-items: flex-start;
  padding:14rem 0px;

  
}

.colum-4 h1{
  font-size: 3.5rem;
  line-height: 60px;
  margin: 20px 0;
  font-weight: 800;
  color: #D5D5C2;
  font-size: 5rem;
}

.colum-4 h3{
  font-size: 1.5rem;
  font-weight: 800;
}

.colum-4 p{
  margin: 1rem;
  font-weight: 500;
  line-height: 1.5rem;
 
}




/* -----------------About section--------------------- */

.about {
  display: flex;
  margin: 50px 70px;
}

.about >div{
  margin: 2rem;
}
.about-a {
  flex-basis:50%;

}
 .about-b{
  flex-basis:50%;
 
 }


.about h3{
  color:rgba(37,37,37,0.7);
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

.about h2{
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.about p {
  font-size: 1.2rem;
  columns: #8b8b8b;
  font-weight:300;
  text-align: justify;
}

.about__btn{
  margin-top: 20px;
  overflow: hidden;
}

.link__btn {
  cursor: pointer;
  width: 8rem;
  text-decoration: none;
  border: none;
  outline: none;
  color: rgba(0, 0, 0);
  font-weight: 700;
  border-radius: 1.8rem;
  font-size: 1rem; 
  padding: 1rem 1rem;
  background: #D8D7C3;
}

.about__img img{
  width: 100%;
  height: 350px;
  box-shadow: 10px 10px;
}





/* ----- Price -------- */

.small-container{
  max-width: 1000px;
  margin: auto;
  padding-left: 25px;
  padding-right: 25px;
 
}


.small-container .col-4{
  flex-basis: 23%;
  padding:10px;
  cursor: pointer;
  margin: 5px;
  margin-bottom: 2rem;
  transition: transform 0.5s;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.col-4 img{
  width: 100%;
} 

.small-container h2{
  font-size: 4rem;
}

.title{
  text-align: center;
  margin: 0 auto 70px;
  position: relative;
  line-height: 60px;
  color: #555;
}

.title h4{
  color:#555;
  font-weight: 800;
}

.col-4 p{
  font-size: 14px;
}

.rating .fa{
  color:#ff523b
}

.col-4:hover{
  transform: translateY(-5px);
}

.title{
  text-align: center;
  margin: 0 auto 70px;
  position: relative;
  line-height: 60px;
  color: #555;
}

h2.title::after{
  content: '';
  background: #555;
  width: 80px;
  height: 5px;
  border-radius: 50px;
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
}

/* --------Gallery ----------- */

.title{
  font-size: 4rem;
  cursor: pointer;
}
.gal-1{
  grid-area: myArea;
}

.gallery {
  display: grid;
  grid-template-areas: 'myArea .' 'myArea .';
  grid-gap: 10px;
  padding:0rem 8rem 5rem;
}

.gallery> div {

  text-align: center;
  padding: 20px 0;
  font-size: 30px;
  width: auto;
  height: 20rem;
}


.gallery >.gal-1{
  background-image: url(img/img.jpg);
  background-size:cover;
  width: auto;
  height: 580px;
}

.gallery >.gal-2{
  background-image: url(img/img1.jpg);
  background-size:cover;
  width: auto;
  height: auto;
}

.gallery >.gal-3{
  background-image: url(img/img2.jpg);
  background-size:cover;
  width: auto;
  height: auto;
}



/* ----------- Footer-------- */
.footer{
  width: 100%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-image: url("./img/temp.jpg");
}
.footer{
  background: #000;
  color: #8a8a8a;
  font-size: 14px;
  padding: 60px 0 20px;
}

.footer p{
  color: #8a8a8a;
}

.footer h3{
  color:#fff;
  margin-bottom: 20px;
}

.footer-col-1,.footer-col-2,.footer-col-3,.footer-col-4{
  min-width: 250px;
  margin-bottom: 20px;
}

.footer-col-1{
  flex-basis: 30%;
}

.footer-col-2{
  flex:1;
  text-align: center;
}

.footer-col-2 img{
  width: 180px;
  margin-bottom:20px
}

.footer-col-3,.footer-col-4{
  flex-basis: 12%;
  text-align: center;
}

ul{
  list-style-type: none;
  cursor: pointer;
}

.app-logo{
  margin-top: 20px;
}

.app-logo img{
  width:140px;
}

.footer hr{
  border:none;
  background:#b5b5b5;
  height:1px;
  margin: 20px 0;
}

.copyright{
  text-align:center;
}
.menu-icon{
  width: 28px;
  margin-left: 20px;
  display: none;
}
