@media only screen and (max-width:600px){
  *{
    box-sizing: border-box;
  }
  body{
    overflow-x: hidden;
  }

  div.colum-4{
   padding: 130px 30px 175px;
  }

  ul#MenuItems{
    display: inline-flex;
  }
  
  .logo img{
  margin: 0px 100px;
  }

  div.colum-4 h1{
    font-size: 75px;  
  }
  
  div.about{
    margin: 50px 20px;
    text-align: center;
  }

  div.about-a{
    flex-basis: 100%;
  }

  div.about img{
    margin-top: 20px;
  }

  div.link__btn{
    margin:0px 100px;
  }
  

  div.about-b{
    display: none;
  }

#prices >.row{
  margin:0px 20px
}
  .row{
      text-align: center;
  }
  .col-2,.col-3,.col-4{
      flex-basis: 100%;
  }
  
  .cart-info p{
      display: none;
  }

  div#gallery.gallery{
    padding: 0px 20px 70px;
  }

  .footer-col-1,.footer-col-3,.footer-col-4{
    margin: 0px 70px;
 
  }
  .footer-col-2,.footer-col-3,.footer-col-4{
    text-align: center;
  }
  
}